.cart-main{
    width: 100%;
    height: 100vh;
    overflow: scroll;
}
table{
    table-layout:fixed;
    height: auto;
}
.cart-empty{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
}

.cart-container {
  display: flex;
  flex-direction: row;
  padding-top: 63px;
  justify-content: space-between;
  height: 100vh;
  width: 100%;
  background-color: #fafafa;
  
}
.cart-products-info{
    width: 75%;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    border: 1px solid #e6e6e6;
    background-color: rgb(255, 255, 255);
}
.cart-billinfo {
    width: 25%;
    height: 100%;
    background-color: rgb(226, 218, 218);
    position: fixed;
    right: 1%;
}
.cart-info-tag{
    display: flex;
    flex-direction: row;
    color: rgb(0, 0, 0);
    padding-top: 2%;
    padding-bottom: 3%;
    justify-content:space-between;
    align-items: center;
    border-bottom:2px solid #dee2e6;
    width: 95%;
  
}
.cart-info-list{
    width: 95%;
}
.cart-info-list table {
    
    width: 100%;
     overflow: scroll;
}
.cart-info-list table tr {
    font-size: larger;
    margin-right: 70px;
    
}

 
.cart-ceanter-td{
    text-align: center;
    vertical-align: top; 
    padding: 1rem; 
} 

.cart-table-head-border{
    padding: 10px;
    font-size: 18px;
    color: rgb(3, 3, 3);
    border-bottom:2px solid #dee2e6;
}
.cart-table-body-productname{
    display: flex;
    flex-direction: row;
   
}
.cart-table-body-productname h4{
    font-size: 20px;
    margin: 5px 10px;
    font-weight: 700;
}


.cart-table-body-productname img{
    height: 4.5em;
}
.cart-table-body-quantiy{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    height: 10%;
}
.cart-table-body-quantiy span{
    width:50%;
    background-color: white;
    border: 1px solid black;
    padding: 6px;
    
}
/* .cart-table-body-quantiy input{
    width:auto;
    height: auto;
    cursor: pointer;
    border: 1px solid rgb(124, 121, 121);
    
} */
.clear-cart{
    padding: 6px ;
    color: rgb(255, 0, 0);
    font-size: 13px;
    border: 1px solid rgb(243, 24, 24);
    background-color: rgb(255, 255, 255);
    
    cursor: pointer;
}
.clear-cart:hover{
    color: rgb(255, 255, 255);
    border: 1px solid rgb(255, 6, 6);
    background-color: rgb(224, 0, 0);
}
.remove-from-cart{
    padding: 8px ;
    color: rgb(255, 255, 255);
    font-size: 13px;
    border: none;
    background-color: rgb(252, 10, 10);
    
    cursor: pointer;
    border-radius: 5px;

}
.cart-billinfo-body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2%;
    
}
tbody{
    overflow: scroll;
}

@media (max-width:840px){
.cart-container {
  display: flex;
  flex-direction: column;
  padding-top: 73px;
  justify-content: space-between;
  width: 100%;
  height: 100vh;
  background-color: #fafafa;
  }.cart-products-info{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80%;
    border: 1px solid #e6e6e6;
    background-color: rgb(255, 255, 255);
   
  }.cart.cart-billinfo{
      width: 100%;
  }.cart-billinfo {
    width: 100%;
    height:20%;
    position: fixed;
    bottom: 0;
  
 }.cart-info-tag{
    padding-top: 5%;
} 

}
